<template lang="jade">
div
  popup
  div.header-container
    img.logo(src='../../../static/images/imaginex.svg')
  div.container--full
    h1 MLA Voting Records
    p Think your MLA represents your voice in the BC legislature in Victoria? Think again. The BC legislature voting data shows that unless your MLA is an independent member, their votes are usually whipped to party lines - meaning that your MLA is representing party politics in Victoria back to you. Use the interactive app below to discover your MLA's voting record.
    instructions
  .row.container--full
    .six.columns
      filters.u-margin-bottom--large
    .six.columns
      mla-legend
      label HIGHLIGHTED VOTE
      p.vote-name {{ title }}
  span.rotate YAY
  span.rotate.rotate--bottom NAY
  span.rotate.rotate--right YAY
  span.rotate.rotate--bottom.rotate--right NAY
  .row.container--full
    whip-canvas

</template>

<script>
import MlaLegend from './MlaLegend'
import Instructions from './Instructions'
import Filters from './Filters'
import WhipCanvas from './whipCanvas/index.vue'
import Popup from './popup'

export default {
  components: {
    MlaLegend,
    Instructions,
    Filters,
    WhipCanvas,
    Popup
  },
  computed: {
    title(){
      let bill = this.$store.state.visualizeVotes.bill
      if (bill.bill){
        return bill.bill + ' — ' + bill.motion
      } else {
        return 'Hover over or press a column to retrieve vote name'
      }
    }
  }
}

</script>
<style lang='stylus'>
@import "../../styles/main";

.rotate {
    position: absolute;
    transform: rotate(-90deg);
    transform-origin: 100% 100%;
    margin-top: 22vh;
    z-index: z-index-2;
    font-size: 12px;
    color: gray;
    margin-left: -0.1rem;
}

.rotate--bottom {
    margin-top: 69vh;
    margin-left: -0.3rem;
}

.rotate--right {
    right: 0;
    margin-left: 0rem;
    margin-right: -0.1rem;
}

.label--expand {
  display: inline-block;
  margin-right: 1rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.vote-name {
    height: 5rem;
}

</style>



