<template lang="jade">
#instructions
  h6.label--expand HOW TO USE THE APP
  div.instructions
    ul
      li The data displayed are of recorded standing votes in 2013 to 2016 from the first to fifth session of the 40th Parliament of the BC Legislature.
      li Each column is a recorded standing vote on a motion pertaining to a bill or an item, such as a budget. Click on a column to discover the details of the vote.
      li Each dot represents an MLA who voted.
      li If the dot is above the middle line, the MLA voted 'YAY'. Otherwise, they voted 'NAY'.
      li The coloured rectangle at the top of a column represents the party of the MLA who initiatiated the vote (mover of the motion).
    p Data source: Hansard 2013 - 2016, Legislative Assembly of British Columbia
  hr

</template>

<script>
export default {
  data() {
    return {
      isOpen: false,
    }
  },
  methods: {
    toggleInstructions() {
      this.isOpen = !this.isOpen;
    },
  }
}
</script>

<style lang='stylus' scoped>
@import "../../styles/main";

</style>



